import { Dialog, TextArea, Toast } from 'antd-mobile';
import styles from '../../../../css/CycleSummaryDialog.module.css';

export function showCycleSummaryDialog(
  cycleNumber: number, 
  onSave: (summary: string) => void
) {
  let summaryText = '';
  
  Dialog.confirm({
    title: ` ${cycleNumber}`,
    content: (
      <div className={styles.content}>
        <div className={styles.label}>
          
        </div>
        <TextArea
          placeholder=''
          maxLength={100}
          rows={3}
          showCount
          onChange={(val) => summaryText = val}
          style={{
            '--font-size': '14px'
          } as any}
        />
      </div>
    ),
    confirmText: '',
    cancelText: '',
    onConfirm: () => {
      if (summaryText.trim()) {
        onSave(summaryText);
        Toast.show({
          icon: 'success',
          content: ''
        });
      }
    }
  });
}

