const copyToClip = (url?: string) => {
  if ( !url ) {
    return false;
  }
  // 
  const share = document.createElement('input');
  share.setAttribute('value', url || '');
  document.body.appendChild(share);
  share.select();
  document.execCommand('copy');
  document.body.removeChild(share);
  return true;
};


const CopyDiv = (props)=>{

  if ( !!props.flag ) {
    return <><span style={{
      textDecoration: 'underline',
      margin: '0 5px',
      color: 'green'
    }}>{props.url}</span><span onClick={()=>{
          if ( copyToClip(props.url) ) {
            Toast.show({
              icon: 'success',
              content: ''
            });
          }
        }}><MailOpenOutline /></span>)
        </>
  }

  return <><a href={props.url} target="_blank" style={{
    textDecoration: 'underline',
    margin: '0 5px',
    color: 'green'
  }}>{props.url}</a><span onClick={()=>{
        if ( copyToClip(props.url) ) {
          Toast.show({
            icon: 'success',
            content: ''
          });
        }
      }}><MailOpenOutline /></span>)
      </>
}

function strSplit(str){
  const strArr = String(str).split('.');
  let t = [
    strArr[0]
  ];

  if ( strArr[1] ) {
    t.push(
      strArr[1].substring(0, 2)
    );
  }

  return t.join('.');
}

