import { createContext, useContext, useState, useEffect, useCallback, ReactNode } from 'react';

const STORAGE_KEY = 'dc_theme';

// 
export const themePresets = {
  default: {
    name: '',
    primary: '#000000',
    primaryHover: '#333333',
  },
  green: {
    name: '',
    primary: '#8CB369',
    primaryHover: '#7AA35A',
  },
  yellow: {
    name: '',
    primary: '#F4E285',
    primaryHover: '#E5D376',
  },
  darkGreen: {
    name: '',
    primary: '#5B8C5A',
    primaryHover: '#4A7B49',
  },
  blue: {
    name: '',
    primary: '#5B9BD5',
    primaryHover: '#4A8AC4',
  },
  purple: {
    name: '',
    primary: '#9B8EC4',
    primaryHover: '#8A7DB3',
  },
  coral: {
    name: '',
    primary: '#F08080',
    primaryHover: '#E07070',
  },
};

export type ThemeKey = keyof typeof themePresets;

interface ThemeContextValue {
  currentTheme: ThemeKey;
  themeColors: typeof themePresets.default;
  setTheme: (theme: ThemeKey) => void;
}

const ThemeContext = createContext<ThemeContextValue | null>(null);

//  localStorage 
const loadThemeFromStorage = (): ThemeKey => {
  try {
    const stored = localStorage.getItem(STORAGE_KEY);
    if (stored && stored in themePresets) {
      return stored as ThemeKey;
    }
    return 'default';
  } catch (error) {
    console.error('Failed to load theme from localStorage:', error);
    return 'default';
  }
};

//  localStorage
const saveThemeToStorage = (theme: ThemeKey) => {
  try {
    localStorage.setItem(STORAGE_KEY, theme);
  } catch (error) {
    console.error('Failed to save theme to localStorage:', error);
  }
};

interface ThemeProviderProps {
  children: ReactNode;
}

export function ThemeProvider({ children }: ThemeProviderProps) {
  const [currentTheme, setCurrentTheme] = useState<ThemeKey>('default');

  //  localStorage 
  useEffect(() => {
    const loadedTheme = loadThemeFromStorage();
    setCurrentTheme(loadedTheme);
  }, []);

  const setTheme = useCallback((theme: ThemeKey) => {
    setCurrentTheme(theme);
    saveThemeToStorage(theme);
  }, []);

  const value: ThemeContextValue = {
    currentTheme,
    themeColors: themePresets[currentTheme],
    setTheme,
  };

  return (
    <ThemeContext.Provider value={value}>
      {children}
    </ThemeContext.Provider>
  );
}

export function useTheme() {
  const context = useContext(ThemeContext);
  if (!context) {
    throw new Error('useTheme must be used within a ThemeProvider');
  }
  return context;
}

export default ThemeContext;
