import Fingerprint2 from 'fingerprintjs2';
import localforage from 'localforage';
import axios from 'axios';

// const prefix = location.href.indexOf('localhost') !== -1 ? '' : 'https://api.jixiang.chat';
const prefix = 'https://api.jixiang.chat';

const prepre = {};

const GLOBAL_PAGE_INFO = {

};

export function createFingerprint() {
    return new Promise<void>((resolve, reject) => {
        // 
        const options = {
            preprocessor: function(key, value) {

                if ( key !== 'canvas' && key !== 'webgl' ) {
                  prepre[key] = value;
                }

                if ( key == "userAgent" ) {
                   return '';
                }
                // console.log(key, value, 'prepe')
                if ( key === 'availableScreenResolution' ) {
                    return [];
                }
                if ( key === 'screenResolution' ) {
                    return [];
                }
                if ( key === 'hasLiedOs' || key === 'hasLiedBrowser' ) {
                    return true;
                }
                if ( key === 'plugins' ) {
                    return [];
                }

                // if ( key === 'platform' ) {
                //    if ( value === 'MacIntel' || value === 'iPad' || value === 'iPhone' ) {
                //      return 'apple';
                //    }
                // }

                if ( key === 'language' ) {
                    return 'zh-CN';
                }

                // if ( key === 'touchSupport' ) {
                //     return [0, false, false];
                // }

                return value;
            }
        };
        Fingerprint2.get(options, (components) => { // 
            const values = components.map(component => component.value); // 
            const murmur = Fingerprint2.x64hash128(values.join(''), 31); // 
            // console.log(components);
            // console.log(values);
            // console.log(murmur);
            resolve(murmur);
            // localStorage.setItem('browserId', murmur); // 
        });
    })
}

export function getDeviceId() {
    var deviceId = '';
    try{
        // 
        if (navigator.userAgent.indexOf('Android') > -1 || navigator.userAgent.indexOf('Adr') > -1) {
            deviceId = window.AndroidWebView.getIMEI();
        }
        // ios
        if (!!navigator.userAgent.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)) {
            deviceId = window.webkit.messageHandlers.getIMEI.postMessage(null);
        }
    }catch(e){}

    try{
        if ( deviceId === '' ) {
            if (navigator.userAgent.match(/Android/i)) {
                // AndroidIMEI
                deviceId = device.getIMEI(); // device.getIMEI()IMEI
            } else if (navigator.userAgent.match(/iPhone|iPad|iPod/i)) {
                // iOSUUID
                deviceId = device.getUUID(); // device.getUUID()UUID
            }
        }
    }catch(e){}

    return deviceId;
}


export async function saveUuid(datas){
  const uuid = await localforage.getItem('robot_accout_id');
 const namelist = await axios(`${prefix}/api/btc/list?apitype=vistedDevice&apitag=CHATGPT`, {
    params: {
      uuid,
      ...datas || {}
    },
    method: 'get'
 });
 return namelist;
}


export const setPageInfo = (key, value)=>{
  GLOBAL_PAGE_INFO[key] = value;
}

export const getPageInfo = (key)=>{
  return GLOBAL_PAGE_INFO[key];
};


