import { memo, useMemo, ReactNode } from 'react';
import styles from './styles.module.css';

interface StatCardProps {
  /**  */
  label: string;
  /**  */
  value: string | number;
  /**  */
  unit?: string;
  /**  */
  icon?: ReactNode;
  /** / */
  description?: string;
  /**  */
  trend?: 'up' | 'down' | 'neutral';
  /**  */
  trendValue?: string | number;
  /**  */
  backgroundColor?: string;
  /**  */
  className?: string;
  /**  */
  onClick?: () => void;
}

/**
 * 
 *  memo 
 */
function StatCardComponent({
  label,
  value,
  unit,
  icon,
  description,
  trend,
  trendValue,
  backgroundColor,
  className,
  onClick
}: StatCardProps) {
  //  useMemo 
  const containerStyle = useMemo(() => ({
    backgroundColor: backgroundColor || undefined,
    cursor: onClick ? 'pointer' : undefined
  }), [backgroundColor, onClick]);

  const trendClassName = useMemo(() => {
    if (!trend) return '';
    return trend === 'up' ? styles.trendUp : trend === 'down' ? styles.trendDown : styles.trendNeutral;
  }, [trend]);

  const classNames = [
    styles.container,
    onClick && styles.clickable,
    className
  ].filter(Boolean).join(' ');

  return (
    <div 
      className={classNames}
      style={containerStyle}
      onClick={onClick}
    >
      {icon && <div className={styles.icon}>{icon}</div>}
      <div className={styles.content}>
        <div className={styles.label}>{label}</div>
        <div className={styles.valueRow}>
          <span className={styles.value}>{value}</span>
          {unit && <span className={styles.unit}>{unit}</span>}
        </div>
        {description && <div className={styles.description}>{description}</div>}
        {trend && trendValue !== undefined && (
          <div className={`${styles.trend} ${trendClassName}`}>
            {trend === 'up' && ''}
            {trend === 'down' && ''}
            {trend === 'neutral' && ''}
            <span className={styles.trendValue}>{trendValue}</span>
          </div>
        )}
      </div>
    </div>
  );
}

//  memo  props 
export const StatCard = memo(StatCardComponent);
export default StatCard;

/**
 * 
 */
interface StatCardGridProps {
  children: ReactNode;
  columns?: 2 | 3 | 4;
  gap?: number;
  className?: string;
}

function StatCardGridComponent({
  children,
  columns = 2,
  gap = 12,
  className
}: StatCardGridProps) {
  const style = useMemo(() => ({
    display: 'grid',
    gridTemplateColumns: `repeat(${columns}, 1fr)`,
    gap: `${gap}px`
  }), [columns, gap]);

  return (
    <div className={className} style={style}>
      {children}
    </div>
  );
}

export const StatCardGrid = memo(StatCardGridComponent);
