import { memo, useMemo } from 'react';
import styles from './styles.module.css';

interface ProgressBarProps {
  /**  0-100 */
  progress: number;
  /**  */
  height?: number;
  /**  */
  color?: string;
  /**  */
  backgroundColor?: string;
  /**  */
  animated?: boolean;
  /**  */
  striped?: boolean;
  /**  */
  borderRadius?: number;
  /**  */
  className?: string;
}

/**
 * 
 *  memo 
 */
function ProgressBarComponent({
  progress,
  height = 8,
  color = 'linear-gradient(90deg, #6366f1 0%, #8b5cf6 100%)',
  backgroundColor = '#e5e7eb',
  animated = false,
  striped = false,
  borderRadius,
  className
}: ProgressBarProps) {
  //  useMemo 
  const progressStyle = useMemo(() => {
    const clampedProgress = Math.min(100, Math.max(0, progress));
    return {
      width: `${clampedProgress}%`,
      background: color,
      borderRadius: borderRadius ?? height / 2
    };
  }, [progress, color, borderRadius, height]);

  const containerStyle = useMemo(() => ({
    height,
    backgroundColor,
    borderRadius: borderRadius ?? height / 2
  }), [height, backgroundColor, borderRadius]);

  const classNames = [
    styles.container,
    className
  ].filter(Boolean).join(' ');

  const progressClassNames = [
    styles.progress,
    animated && styles.animated,
    striped && styles.striped
  ].filter(Boolean).join(' ');

  return (
    <div className={classNames} style={containerStyle}>
      <div 
        className={progressClassNames}
        style={progressStyle}
      />
    </div>
  );
}

//  memo  props 
export const ProgressBar = memo(ProgressBarComponent);
export default ProgressBar;
